.container {
    width:100%;
    height:100vh;
    padding-top: 50px;
    background-color: gray;
}
.weather {
    color: beige;
    width:380px;
    padding:20px;
    border-radius:20px;
    margin:10px auto;
    text-align: center;
    background: linear-gradient(130deg,rgba(37, 37, 37, 0.888),rgb(27, 27, 30));
    border-style:solid;

}
.search {
    display:flex;
    align-items: center;
    justify-content: space-between;
}
.search input {
    border:none;
    outline:0;
    padding:12px;
    font-size:18px;
    background-color: rgb(222,237,243);
    color:black;
    border-radius: 30px;
    flex:1;
    margin-right: 12px;
}

.search button {
    border:none;
    outline:0;
    width:50px;
    height:50px;
    border-radius: 50%;
    cursor: pointer;
}

.search button img {
    width:20px;
}

.winfo img {
    margin-top: 20px;
}
#weather {
    height:100px;
}

.winfo h1 {
    font-size:70px;
    font-weight:500;
    margin-top:5px;
}

.winfo h2 {
    font-size:40px;
    font-weight:400;
    margin-top:-40px;
}

.details {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0px 20px;
    margin-top: 50px;
}
.details col{
    display: flex;
    align-items: center;
    text-align: left;
}
.details .col img {
    width:45px;
    margin-right: 10px;
}

.humidity, .wind {
    font-size: 27px;
    
}

.error {
    text-align: center;
    margin-right: 10px;
}